  methods:{
    getCookie (name) {
      var value = '; ' + document.cookie
      var parts = value.split('; ' + name + '=')
      if (parts.length === 2) return parts.pop().split(';').shift()
    },
    keyup_submit () {
      var evt = window.event; 
      if (evt.keyCode == 13){
        console.log(this.keycode)
        this.axios.post('/api/login',{keycode:this.keycode},
        {headers: {'X-CSRFToken': this.getCookie('csrftoken'),'Content-Type':'application/x-www-form-urlencoded'}}).then((res)=>{
          console.log(res.data)
        })
      }
    },
    rotate_second () {
      for(var i=0;i<60;i++){
        this.second[i].deg +=1;
      }
    },
    rotate_minute () {
      for(var i=0;i<60;i++){
        this.minute[i].deg +=1;
      }
    },
    rotate_hour () {
      for(var i=0;i<24;i++){
        this.hour[i].deg +=1;
      }
    },
    rotate_day () {
      for(var i=0;i<31;i++){
        this.day[i].deg +=1;
      }
    },
    rotate_month () {
      for(var i=0;i<12;i++){
        this.month[i].deg +=1;
      }
    }
  },
  mounted () {
    var second = 0;
    var minute = 0;
    var hour = 0;
    var day = 0;
    var month = 0;
    setInterval(() => {
      this.rotate_second();
      if(second==59){
        this.rotate_minute()
        if(minute==59){
          this.rotate_hour()
          if(hour==23){
            this.rotate_day()
            if(day==30){
              this.rotate_month()
              month +=1
            }
            day += 1
            hour = -1
          }
          hour += 1
          minute = -1
        }
        minute +=1
        second = -1
      }
      second +=1
    }, 1000);

  }
